.card__title {
  text-transform: capitalize;
  border-top: 2px solid grey;
  border-bottom: 2px solid grey;
  padding: 10px;
}

.card__types {
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
}
.card__type {
  grid-column: span 1;
  text-align: center;
  text-transform: capitalize;
}
